import { Helmet } from "react-helmet-async";
import GalleriePhoto from "../../components/GalleriePhoto/GalleriePhoto";
import styles from "./Photography.module.css";

export default function Photography() {
  return (
    <div className={styles.container}>
      <Helmet>
        <title>Photography - Portfolio Riad</title>
        <meta
          name="description"
          content="gallerie de la collection photographique de riad en tant que photographe amateur"
        />
      </Helmet>
      <GalleriePhoto />
    </div>
  );
}
